.globe-container,
.globe {
  width:300px;
  height: 300px;
  /* border: 3px solid black; */
}
.globe-container {
  position: relative;
  display: inline-block;
  margin: 30px;
  transform: scale(1.1);
}
.globe {
  position: relative;
  display: block;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
}
.globe-worldmap,
.globe-worldmap-front,
.globe-worldmap-back,
.globe-sphere,
.globe-outer-shadow,
.globe-inner-shadow {
  position: absolute;
  display: block;
  margin: 0;
}
.globe-sphere,
.globe-outer-shadow,
.globe-inner-shadow {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-position: 0 0;
  background-repeat: no-repeat;
}
.globe-worldmap {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 50%;
}
.globe-worldmap-front,
.globe-worldmap-back {
  left: 0;
  top: 15%;
  width: 1000px;
  height: 200px;
  overflow: visible;
  background-image: url(../../../assets/hero/worldmap.svg);
  background-repeat: no-repeat;
  /* background-position-y: bottom !important; */
  /* background-size: cover; */
}
/* .globe-outer-shadow {
  left: 0;
  top: 186px;
  width: 200px;
  height: 30px;
  background-image: url(outer_shadow.svg);
} */
.globe-inner-shadow {
  background-image: url(../../../assets/hero/inner_shadow.svg);
}
.globe-sphere {
    background-image:url(../../../assets/hero/sphere.svg)
}

.globe-worldmap-front {
  background-position: 0px 0px;
  animation: textureSpinreverse 8s linear infinite;
}
.globe-worldmap-back {
  background-position: 0px -220px;
  animation: textureSpin 8s linear infinite;
}

@keyframes textureSpin {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-47.5%);
  }
}
@keyframes textureSpinreverse {
  from {
    transform: translateX(-47.5%);
  }
  to {
    transform: translateX(0);
  }
}

@media only screen and (max-width: 425px) {
  .globe-container{
    display: none;
  }
  
}
